@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Open+Sans&display=swap);
body {
  all: revert;
}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: lightgray;
}
body {
  margin: 0 auto !important;
  font-family: "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
}

.App{padding:20px;text-align:left}.won-card{background-color:#157b19}.loss-card{background-color:#8e2020}.draw-card{background-color:#767679}.app-name{font-size:2.5rem;margin:30px auto}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-details{text-align:right}.MatchDetailCard .additional-details>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:flex-start;align-self:flex-end}.TeamPage .team-name-section .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-draw-section{justify-self:center;padding:30px;text-align:center}.TeamPage .match-detail-section{grid-column:span 4}.more-link-card{align-self:center;justify-self:center;font-size:1.5rem}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:flex-end;padding:20px}.MatchPage .page-heading{margin:20px auto}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTile{height:180px;text-align:center;border:3px solid gray;padding:20px;background-color:#8d71c0}.TeamTile a{color:black !important}

