.TeamPage {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 10px;

  .team-name-section {
    grid-column: span 3;
    justify-self: flex-start;
    align-self: flex-end;

    .team-name {
      font-size: 4rem;
      margin-bottom: 20px;
    }
  }

  .win-loss-draw-section {
    justify-self: center;
    padding: 30px;
    text-align: center;
  }

  .match-detail-section {
    grid-column: span 4;
  }
}

.more-link-card {
  align-self: center;
  justify-self: center;
  font-size: 1.5rem;
}
